                if (Array.isArray(response.data)) {
                    item_list_category.value = response.data.map((item) => ({
                        id: item.id,
                        name: item.name,
                    }));
                } else {
                    console.error("Invalid response format");
                }
            })
            .catch((error) => {
                console.error("Error fetching item list category:", error.message);
            });
    };

    const type = ref('');
    const machine_id = ref('0');
    const registerItem = () => {
        isSending.value=true;
        loading_spinner.value = true;
   
      
        // if (category.value === 'others') { 
        //     description.value = description.value;           
        // }
        // else {
            if (asset.value) {
              description.value = category.value + ' for ' + asset.value.name + ' : ' +task.value;
