            description: description.value,
            unit_measure:unit.value,
            quantity:quantity.value,
            machine_id: asset.value.id,
            rate:rate.value,
            item_list_id: item_list_id.value,
            category :category.value,
            specification : type.value.name,
        };

        axiosInstance
