            .then((response) => {
                getItems();
                isSending.value= false;
                loading_spinner.value = false;
                if (response.status === 201) {
                    clearItemForm();
                   showMessage('Item registered successfully');
                } else {
                    showError('Error registering item');
                    console.error("Error registering Order:", response.data);
                }
            })
            .catch((error) => {
                 showError(error.message);
                isSending.value= false;
                console.error("Error registering Order:", error);
            });
    };

    const deleteItem = (id) => {
        loading_spinner.value = true;
   if (confirm("Are you sure you want to delete this item?")) {
                  axiosInstance
            .delete(`/items/${id}`)
            .then((response) => {
                getItems();
                loading_spinner.value = false;
                if (response.status === 200) {
                   showMessage('Item Deleted successfully');
                } else {
                    showError('Fail to Delete item');
                    console.error("Error registering Order:", response.data.message);
                }
            })
            .catch((error) => {
                console.error("Error registering Order:", error.message);
            });  
    
   }
    };
 
    const clearItemForm = () => {
        isSending.value = false;
        description.value = '';
        unit.value = '';
        quantity.value = '';
        rate.value = '';
        item_list_id.value = '';
        machine_id.value = '0';
        category.value = '';
        type.value = '';
        asset.value = '';
        task.value = ''
    };





// lpo modal ends


